import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import ProductType from "../../products/models/product.type";
import AppStore from "..";
import http from "../../http";
import ProductResult from "../../products/models/product-result.type";
import ToastrStore from "./toastr-store.slice";
namespace ProductStore {
  export const searchProductList = createAsyncThunk("product/searchProductList", async (searchKeyword: string) => {
    const response = await http.get<ProductResult>(`products/search?q=${searchKeyword}&limit=100&skip=0`);
    return response.data;
  });
  export const getProductCategories = createAsyncThunk("product/getProductCategories", async () => {
    const response = await http.get<string[]>(`products/categories`);
    return response.data;
  });
  type ProductStoreType = {
    productList: ProductType[];
    productCategories: string[];
  };
  const initialState: ProductStoreType = {
    productList: [],
    productCategories: [],
  };
  const productStoreSlice = createSlice({
    initialState,
    name: "product",
    reducers: {
      setProductList(state, action) {
        state.productList = action.payload;
      },
      reset(state) {
        state.productList = [];
      },
    },
    extraReducers: (builder) => {
      builder.addCase(searchProductList.fulfilled, (state, action) => {
        state.productList = action.payload.products;
        if (action.payload.products.length === 0) {
          setTimeout(() => {
            AppStore.store.dispatch(
              ToastrStore.actions.add({
                message:
                  "I couldn't find a product with this search term. You can try another search term. For example 'bag'",
                variant: "warning",
              })
            );
          });
        }
      });

      builder.addCase(getProductCategories.fulfilled, (state, action) => {
        state.productCategories = action.payload;
      });
    },
  });
  export const actions = productStoreSlice.actions;
  export const reducer = productStoreSlice.reducer;
  export const select = {
    getProductList: (state: AppStore.RootState) => state.product.productList,
    getProductCategories: (state: AppStore.RootState) => state.product.productCategories,
  };
}

export default ProductStore;
